import React from 'react';

const inputStyle = {
  width: '100%', padding: '0.85rem 1rem', borderRadius: 10, border: '1px solid #ddd',
  fontSize: '1rem', marginBottom: '1rem', outline: 'none'
};

const ContactSection = () => (
  <section id="contact" style={{ padding: '4rem 1.5rem', background: '#0b0b0b', color: '#fff' }}>
    <div style={{ maxWidth: 900, margin: '0 auto' }}>
      <h2 style={{ textAlign: 'center', marginBottom: '1rem', fontSize: '2rem' }}>Get a Free Quote</h2>
      <p style={{ textAlign: 'center', marginBottom: '2rem', color: '#bbb' }}>
        Tell us a bit about your project. We’ll reply quickly.
      </p>
      <form action="https://formspree.io/f/xanbpmwn" method="POST" style={{
        display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(260px, 1fr))',
        gap: '1rem', background: '#111', padding: '1.25rem', borderRadius: 16, boxShadow: '0 10px 30px rgba(0,0,0,0.35)'
      }}>
        <input style={inputStyle} type="text" name="name" placeholder="Your Name" required/>
        <input style={inputStyle} type="email" name="email" placeholder="Your Email" required/>
        <input style={inputStyle} type="tel" name="phone" placeholder="Your Phone (optional)"/>
        <input style={inputStyle} type="text" name="location" placeholder="City / Area"/>
        <textarea style={{...inputStyle, gridColumn: '1 / -1', minHeight: 140}} name="message" placeholder="What do you need help with? (TV mount, whole home audio, Wi‑Fi, theater, commercial AV…)" required/>
        <button type="submit" style={{
          gridColumn: '1 / -1', background: '#FFD700', color: '#000', border: 'none',
          padding: '0.9rem 1.2rem', borderRadius: 10, fontWeight: 700, fontSize: '1.05rem',
          cursor: 'pointer', boxShadow: '0 8px 20px rgba(255,215,0,0.35)'
        }}>
          Send Request
        </button>
        <input type="hidden" name="source" value="dapperav.com" />
      </form>

      <div style={{ marginTop: '2rem', textAlign: 'center', color: '#bbb' }}>
        Prefer email? <a href="mailto:contact@dapperav.com" style={{ color: '#FFD700', textDecoration: 'none' }}>contact@dapperav.com</a>
        <span style={{ margin: '0 0.6rem' }}>•</span>
        Call/Text: <a href="tel:+19728241303" style={{ color: '#FFD700', textDecoration: 'none' }}>+1 (972) 824‑1303</a>
      </div>
    </div>
  </section>
);

export default ContactSection;
